@import url(https://fonts.googleapis.com/css2?family=Rajdhani:wght@300;500&display=swap);
body {
  margin: 0;

font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body{
  background-color: #023e8a;
  text-align: center;
}
.App{
  background-color: #0077b6;
  min-height:500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input{
  background-color: rgba(173, 232, 244, 1);
  border: 0; 
  color: #fff;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
#to-do-form input::-webkit-input-placeholder{
  color: rgba(2, 62, 138, 0.5);
}
#to-do-form input:-ms-input-placeholder{
  color: rgba(2, 62, 138, 0.5);
}
#to-do-form input::placeholder{
  color: rgba(2, 62, 138, 0.5);
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color:rgba(2, 62, 138, 1); 
  cursor: pointer;
  font-weight: 500;
  font-size: 1em;
  color: white;
  text-shadow: 1px 1px 5px black;
}
#to-do-form button:focus{
  outline: none;
}
.list{
  color: #fff;
  width: 300px;
  margin: 20px auto;
  height: 50px;
  background-color: #48cae4;
  border-radius: 5px;
}
.list p{
  padding: 10px;
  position: relative;
  transition: ease-in-out;
}
.list p input{
  background-color: transparent;
  border: 0;
  color: #fff;
  font-size: 18px;
}
.list p input:focus{
  outline: none;
}
.list p span{
  position: absolute;
  right: 10px;
}
.faicons{
  margin: 0 5px;
  font-size: 15px;
  cursor: pointer;
}
.itemsAnim-enter{
  opacity: 0.01;
}
.itemsAnim-enter .itemsAnim-enter-active{
  opacity: 1;
  transition: opacity 300ms ease-in
}
.itemsAnim-leave{
  opacity: 1;
}
.itemsAnim-leave .itemsAnim-leave-active{
  opacity: 0.01;
  transition: opacity 300ms ease-in
}
